"use client";

import React, { useEffect, useState } from "react";
import { useAuth } from "../../context/AuthProvider";
import { Card, CardHeader, CardContent, CardTitle } from "../../components/ui/card";
import { useRouter } from "next/navigation";

interface ResumeData {
  professionalSummary: string;
  education: Array<{
    institution: string;
    degree: string;
    year: string;
    description: string;
  }>;
  workExperience: Array<{
    company: string;
    position: string;
    duration: string;
    description: string;
  }>;
  skills: string[];
  certifications: string[];
  projects: Array<{
    name: string;
    description: string;
  }>;
}

const ResumePage = () => {
  const { user, role } = useAuth();
  const router = useRouter();
  const [resumeData, setResumeData] = useState<ResumeData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    if (!user || role !== "pro") {
      router.push("/login");
    } else {
      // Simulate API fetch
      (async () => {
        try {
          const data: ResumeData = {
            professionalSummary: "Experienced software developer with a strong background in full-stack development and cloud technologies. Proven track record of delivering scalable solutions and leading technical teams.",
            education: [
              { 
                institution: "Stanford University",
                degree: "Master of Science in Computer Science",
                year: "2018-2020",
                description: "Specialized in Artificial Intelligence and Machine Learning"
              },
              {
                institution: "University of California, Berkeley",
                degree: "Bachelor of Science in Computer Science",
                year: "2014-2018",
                description: "Minor in Business Administration"
              }
            ],
            workExperience: [
              {
                company: "Google",
                position: "Senior Software Engineer",
                duration: "2020-Present",
                description: "Lead developer for cloud infrastructure projects, managing a team of 5 engineers. Implemented microservices architecture that improved system reliability by 40%."
              },
              {
                company: "Microsoft",
                position: "Software Engineer",
                duration: "2018-2020",
                description: "Developed and maintained enterprise-level applications using .NET Core and Azure. Reduced deployment time by 60% through CI/CD pipeline optimization."
              }
            ],
            skills: [
              "JavaScript/TypeScript",
              "React/Next.js",
              "Node.js",
              "Python",
              "AWS/Azure",
              "Docker/Kubernetes",
              "System Design",
              "Team Leadership"
            ],
            certifications: [
              "AWS Solutions Architect Professional",
              "Google Cloud Professional Developer",
              "Microsoft Azure Solutions Expert"
            ],
            projects: [
              {
                name: "Cloud Migration Platform",
                description: "Led the development of an automated cloud migration platform that helped 100+ enterprise clients transition to cloud infrastructure."
              },
              {
                name: "AI-Powered Analytics Dashboard",
                description: "Designed and implemented a real-time analytics dashboard using machine learning algorithms for predictive insights."
              }
            ]
          };
          setResumeData(data);
        } catch (err) {
          console.error(err);
          setError("Failed to load resume data. Please try again later.");
        } finally {
          setLoading(false);
        }
      })();
    }
  }, [user, role, router]);

  const handleDownload = async () => {
    try {
      const response = await fetch("/api/resume/download");
      const data = await response.json();
      
      if (response.status === 501) {
        alert(data.message);
      } else if (!response.ok) {
        throw new Error(data.message || "Failed to download resume");
      }
      // When PDF generation is implemented, we'll handle the PDF download here
    } catch (error) {
      console.error("Download error:", error);
      alert("Failed to download resume. Please try again later.");
    }
  };

  if (loading) return (
    <div className="flex justify-center items-center min-h-[60vh]">
      <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-gray-900"></div>
    </div>
  );
  
  if (error) return (
    <div className="flex justify-center items-center min-h-[60vh]">
      <p className="text-center text-red-500 bg-red-50 px-4 py-2 rounded-md">{error}</p>
    </div>
  );

  if (!resumeData) return null;

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header Section */}
      <div className="relative mb-12">
        <div className="h-48 bg-gradient-to-r from-gray-900 to-gray-800 rounded-lg shadow-lg"></div>
        <div className="absolute -bottom-6 left-8 flex items-end space-x-6">
          <div className="w-24 h-24 bg-white rounded-full border-4 border-white shadow-lg flex items-center justify-center">
            <span className="text-3xl font-bold text-gray-700">
              {(user?.id || "JD").substring(0, 2).toUpperCase()}
            </span>
          </div>
          <div className="mb-6">
            <h1 className="text-3xl font-bold text-white">{user?.id || "John Doe"}</h1>
            <p className="text-gray-200">Professional Resume</p>
          </div>
        </div>
      </div>

      {/* Professional Summary */}
      <Card className="mb-8">
        <CardHeader>
          <CardTitle className="text-xl font-bold">Professional Summary</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-gray-700 leading-relaxed">{resumeData.professionalSummary}</p>
        </CardContent>
      </Card>

      {/* Work Experience */}
      <Card className="mb-8">
        <CardHeader>
          <CardTitle className="text-xl font-bold">Work Experience</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-6">
            {resumeData.workExperience.map((exp, idx) => (
              <div key={idx} className="border-l-2 border-gray-200 pl-4 ml-2">
                <h3 className="font-bold text-lg text-gray-900">{exp.position}</h3>
                <p className="text-gray-700 font-medium">{exp.company}</p>
                <p className="text-gray-500 text-sm">{exp.duration}</p>
                <p className="mt-2 text-gray-600">{exp.description}</p>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Education */}
      <Card className="mb-8">
        <CardHeader>
          <CardTitle className="text-xl font-bold">Education</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-6">
            {resumeData.education.map((edu, idx) => (
              <div key={idx} className="border-l-2 border-gray-200 pl-4 ml-2">
                <h3 className="font-bold text-lg text-gray-900">{edu.degree}</h3>
                <p className="text-gray-700">{edu.institution}</p>
                <p className="text-gray-500 text-sm">{edu.year}</p>
                {edu.description && (
                  <p className="mt-2 text-gray-600">{edu.description}</p>
                )}
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Skills & Certifications */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-8">
        <Card>
          <CardHeader>
            <CardTitle className="text-xl font-bold">Skills</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex flex-wrap gap-2">
              {resumeData.skills.map((skill, idx) => (
                <span
                  key={idx}
                  className="px-3 py-1 bg-gray-100 text-gray-700 rounded-full text-sm"
                >
                  {skill}
                </span>
              ))}
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="text-xl font-bold">Certifications</CardTitle>
          </CardHeader>
          <CardContent>
            <ul className="space-y-2">
              {resumeData.certifications.map((cert, idx) => (
                <li key={idx} className="flex items-center space-x-2">
                  <div className="w-2 h-2 bg-gray-900 rounded-full"></div>
                  <span className="text-gray-700">{cert}</span>
                </li>
              ))}
            </ul>
          </CardContent>
        </Card>
      </div>

      {/* Projects */}
      <Card className="mb-8">
        <CardHeader>
          <CardTitle className="text-xl font-bold">Notable Projects</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-6">
            {resumeData.projects.map((project, idx) => (
              <div key={idx} className="border-l-2 border-gray-200 pl-4 ml-2">
                <h3 className="font-bold text-lg text-gray-900">{project.name}</h3>
                <p className="mt-2 text-gray-600">{project.description}</p>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Download Button */}
      <div className="flex justify-end">
        <button
          onClick={handleDownload}
          className="px-6 py-3 bg-gray-900 text-white rounded-lg hover:bg-gray-800 transition-colors duration-200 flex items-center space-x-2"
        >
          <span>Download Resume (PDF)</span>
        </button>
      </div>
    </div>
  );
};

export default ResumePage;
